body {
  background: linear-gradient(to bottom right, rgb(168, 218, 220), rgb(69, 123, 157));
  color: black;
  min-height: 100vh;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.search-box {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-bottom: 20px;
}

input {
  padding: 10px;
  width: 200px;
  border-radius: 8px;
  border: 1px solid white;
}

button {
  background-color: rgb(0, 120, 255);
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
}

button:hover {
  background-color: rgb(0, 94, 204);
}

.weather-card {
  background-color: white;
  padding: 20px;
  border-radius: 12px;
  width: 250px;
  margin: 0 auto;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}

.error {
  color: red;
  font-weight: bold;
}







